/* NAVBAR ------------------------------------------------------------------- */

.hamburger {
  display: none ;
  cursor: pointer;
  font-size: 2em;
  }
  
  @media (max-width: 768px) {
  .hamburger {
  display: block;
  color: rgb(0, 0, 0);
  background-color: transparent;
  border: aliceblue;
  }
  
  .navbar-collapse {
  height: 0;
  width: 0;
  background: #f2f2f2;
  position: absolute;
  top: 10vh;
  right: 0;
  z-index: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }
  
  .navLink {
  color: white;
  font-size: 30px;
  margin: 1em 0;
  text-align: center;
  padding-right: 0 !important;
  }
  
  .show {
  height: 100vh !important;
  width: 100vw !important;
  display: flex;
  position: absolute;
  top: 0;
  background: lightgrey;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }

  .navbar.open {
    height: 100vh;
    width: 100vw;
  }

  
  }






/* -----------------------------------------------------------------------------NAVBAR */